html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1.6;
  font-family: "Spline Sans", sans-serif;
  font-weight: 300;
  font-size: 16px;
  padding-top: 5rem;
  letter-spacing: 1px;
  background-color: var(--color-main);
}
body.light-mode {
  --font-color: #1e2125;
  --font-color-invert: #eaebed;
  --cyan: #004257;
  --cyan-invert: #0ec7ff;
  --cyan-icons: #00799e;
  --color-box: #ececec;
  --color-card: white;
  --color-main: #cacccc;
  --color-sub: #cacccc;
  --color-accent: #383d46;
  --color-accent-2: #383d46;
  --color-accent-invert: #ececec;
  --footer-gradient: linear-gradient(180deg, #959ba7 -30%, #383d46 17.5%, #383d46 82.5%, #1e2125 100%);
  --shadow-color: #616875;
  --semi-transparent: rgba(255, 255, 255, 0.88);
  --icon-color: #383d46;
  --icon-card-color: #383d46;
  --icon-footer-color: #cacccc;
  --watermark-color: rgb(56, 61, 70, 0.3);
  --portfolio-gradient-color: rgb(20, 20, 23, 0.3);
}
body.dark-mode {
  --font-color: #ececec;
  --font-color-invert: #1e2125;
  --cyan: #0ec7ff;
  --cyan-invert: #004257;
  --cyan-icons: #0ec7ff;
  --color-box: #1e2125;
  --color-card: #141417;
  --color-main: #383d46;
  --color-sub: #616875;
  --color-accent: #cacccc;
  --color-accent-2: #ececec;
  --color-accent-invert: #383d46;
  --footer-gradient: linear-gradient(180deg, #cacccc -15%, #959ba7 17.5%, #959ba7 82.5%, #383d46 140%);
  --shadow-color: #616875;
  --semi-transparent: rgba(30, 33, 37, 0.88);
  --icon-color: #ececec;
  --icon-card-color: #cacccc;
  --icon-footer-color: #383d46;
  --watermark-color: rgb(255, 255, 255, 0.2);
