.main-links {
  font-size: 1.333rem;
  padding: 10px 25px;
  margin: -7px -25px;
  transition: color 150ms ease-in-out;
}
.main-links::after {
  content: "";
  position: absolute;
  left: 0;
  top: 80%;
  height: 4px;
  border-radius: 8px;
  background-image: linear-gradient(to right, #00799e, #0ec7ff);
  width: 0;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out, width 250ms ease-in-out;
}
.main-links.extended::after {
  transform: scaleX(0.4);
  width: 100%;
}
.main-links.shortened::after {
  transform: scaleX(0);
  width: 100%;
}
.main-links:hover, .main-links:focus {
  color: var(--cyan-icons);
}

.main-links.shortened:hover::after, .main-links.shortened:focus::after, .main-links.extended:hover::after, .main-links.extended:focus::after {
  transform: none;
