.hamburger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-right: 5rem;
  padding-inline: 1rem;
}
.hamburger:focus {
  outline: none;
}
@media (max-width: 41.8125em) {
  .hamburger {
    margin-right: 0.7rem;
  }
}
@media (min-width: 30.375em) {
  .hamburger {
    margin-right: 1.5rem;
  }
}
@media (min-width: 41.875em) {
  .hamburger {
    margin-right: 2.5rem;
  }
}
.hamburger > span {
  display: block;
  width: 55px;
  height: 6px;
  border-radius: 1.25px;
  background-color: var(--color-accent-2);
  transition: transform 350ms ease-in-out, width 300ms ease-in-out 300ms, opacity 300ms ease-in-out;
}
.hamburger > span:not(:last-child) {
  margin-bottom: 10px;
}
.hamburger > span:nth-child(2) {
  max-width: 66.6%;
  margin-left: auto;
}
@media (max-width: 30.3125em) {
  .hamburger > span {
    width: 42px;
    height: 5px;
  }
  .hamburger > span:not(:last-child) {
    margin-bottom: 6px;
  }
}

.open {
  transition: transform 300ms ease-in-out 600ms, width 300ms ease-in-out 600ms, opacity 300ms ease-in-out 600ms;
  transform: rotate(45deg);
}
.open > span:not(:nth-child(2)) {
  background-color: #0ec7ff;
}
.open :nth-child(2) {
  width: 0;
  opacity: 0;
}
.open :first-child,
.open :last-child {
  transition: transform 300ms ease-in-out 300ms, background-color 300ms ease-in-out;
}
.open :first-child {
  transform: translateY(16px);
}
@media (max-width: 30.3125em) {
