}
.button.cyan-switch {
  background: linear-gradient(320deg, #0ec7ff -10%, #004257 100%);
}
.button.disabled {
  position: relative;
  border-top: 2.5px solid rgb(118, 118, 118);
  border-right: 2.5px solid rgb(33, 33, 33);
  border-bottom: 2.5px solid rgb(33, 33, 33);
  border-left: 2.5px solid rgb(118, 118, 118);
  text-shadow: none;
}
.button.disabled:focus {
  outline: 2px solid #383d46;
}
.button.disabled:hover, .button.disabled:focus {
  cursor: no-drop;
  box-shadow: 0 0 5px #383d46, 0 0 10px #383d46, 0 0 15px #616875, 0 0 20px #cacccc;
}
.button.disabled:hover::after, .button.disabled:focus::after {
  content: attr(data-tooltip);
  width: 200%;
  font-size: 1rem;
  color: var(--font-color-invert);
  background: var(--color-accent-2);
  padding: 1rem;
  border-radius: 5px;
  display: inline-block;
  position: absolute;
  transform: translate(-50%, -100%);
  margin: 0 auto;
  min-width: 100px;
  min-width: 150px;
  top: -10px;
  left: 50%;
  text-align: center;
}
@media (max-width: 41.8125em) {
  .button.disabled:hover::after, .button.disabled:focus::after {
    width: 150%;
