.slider {
  position: absolute;
  cursor: pointer;
  inset: 0 0;
  border-radius: 4rem;
  background-color: var(--color-accent-2);
  transition: background-color 400ms ease-in-out;
}
.slider::before {
  border-radius: 50%;
}

.slider::before {
  content: "";
  position: absolute;
  height: 2rem;
  width: 2rem;
  left: 0;
  bottom: -4px;
  background-color: var(--color-box);
  transition: transform 400ms ease-in-out, box-shadow 400ms ease-in-out;
}

.media-control {
  position: absolute;
  width: 2rem;
  height: 2rem;
  left: 0;
  bottom: -4px;
  transition: transform 400ms ease-in-out, opacity 200ms ease-in-out;
  fill: var(--color-accent-2);
}
.media-control.play {
  opacity: 1;
  fill: #0ec7ff;
}
.media-control.pause {
  opacity: 0;
}

.auto-play-slideshow:focus + .slider::before, .auto-play-slideshow:hover + .slider::before {
  box-shadow: 0 0 5px #383d46, 0 0 7.5px #00799e, 0 0 10px #0ec7ff, 0 0 15px #5fcbf6;
}

input:not(input:checked) + * + .auto-play-label::before {
  animation: show-shimmer-disappear 5s forwards ease-in-out;
}
input:checked + .slider {
  background-color: #0ec7ff;
}
input:checked + .slider::before {
  transform: translateX(28px);
}
input:checked + .slider > .media-control {
  transform: translateX(28px) rotate(180deg);
}
input:checked + .slider > .play {
  opacity: 0;
}
input:checked + .slider > .pause {
  opacity: 1;
}
input:checked + .slider + .auto-play-label::after {
  animation: show-shimmer-disappear 5s forwards ease-in-out;
}

